<!--助眠音乐播放-->
<template>
    <s-header :name="'舒眠减压-气氛音助眠'"/>
    <div class="div_sleepmusic">
        <div class="div_circle">
            <div class="div_circle2">
                <img src="@/assets/ic_music.png">
                <img class="img_pause" src="@/assets/ic_pause.png" @click="onClick">
            </div>
        </div>
        <span class="span_title">夜雨促眠</span>
        <img src="@/assets/ic_music_bg.png" class="img_bottom"/>

        <div class="div_bottom">
            <div class="div_sleepmusic_item" v-for="(item,index) in mdata" :key="index" @click="()=>onClick(item)"
                 :style="'background-color: '+item.backcolor">
                <span class="span_item_title" :style="'color: '+item.fontcolor">{{item.title}}</span>
                <span class="span_item_des" :style="'color: '+item.fontcolor">{{item.des}}</span>
            </div>
        </div>

    </div>
</template>

<script>
  import {reactive, onMounted, toRefs} from 'vue'
  import sHeader from '@/components/SimpleHeader'

  const {getHelpSleepMusic} = require("../../../../service/sleep");

  export default {
    name: "SleepMusic",
    props: {},
    components: {
      sHeader,
    },
    setup() {
      const initData = async () => {
        //获取助眠音乐数据
        const res = await getHelpSleepMusic()
        if (res.data && res.data.length > 0) {
          for (let i = 0; i < res.data.length; i++) {
            state.mdata[i].title = res.data[i].helpSleepName
            state.mdata[i].musicPath = res.data[i].musicPath
          }
        }
      }
      onMounted(
        () => {
          initData()
        }
      )

      // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
      const state = reactive({
        mdata: [
          {title: '脑波促眠', des: '30:00', backcolor: ' #E2F3E1', fontcolor: '#00BC88'},
          {title: '午后小憩', des: '30:00', backcolor: ' #E3E9DF', fontcolor: '#939E82'},
          {title: '活力早晨', des: '30:00', backcolor: ' #EBE9EA', fontcolor: '#A39E9F'},
          {title: '冥想静心', des: '30:00', backcolor: ' #DCEEF1', fontcolor: '#73AEB8 '},
        ],
        isplay:false,

      })
      const onClick = (item) => {
        let audio = new Audio()
        audio.src = item.musicPath
        audio.play();
      }
      // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
      return {
        ...toRefs(state),
        onClick,
      }
    }
  }
</script>

