import axios, {BASEURL} from '../../utils/axios'


/**
 * 根据日获取睡眠记录
 * @returns {*}
 */
export function getHistorySleepByDay(dateTime) {
  return axios.get(`/monitor/health/sleep/SySleepLog/getHistorySleepByDay?dateTime=${dateTime}`);
}

/**
 * 根据周、月、年获取睡眠记录
 * @returns {*}
 */
export function getHistorySleepByWeek(startTime,endTime) {
  return axios.get(`/monitor/health/sleep/SySleepLog/getHistorySleepByWeek?startTime=${startTime}&endTime=${endTime}`);
}
/**
 * 手动添加
 * @returns {*}
 */
export function manualInput(params) {
  return axios.post(`/monitor/health/sleep/SySleepLog/manualInput`,params);
}
/**
 * 助眠音乐
 * @returns {*}
 */
export function getHelpSleepMusic(params) {
  return axios.get(`/monitor/helpSleep/getHelpSleepMusic`,params);
}
/**
 * 夜间睡眠
 * userId=209928814304821248
 * @returns {*}
 */
export function getReferenceResource(userId) {
  return axios.get(`/monitor/health/sleep/SySleepLog/getReferenceResource?userId=${userId}`);
}

